import React, {forwardRef, PropsWithChildren, PropsWithoutRef, PropsWithRef, ReactNode, Ref} from 'react';
import Heading from '../heading/heading';

import * as styles from './index-module.module.scss';
import BackgroundFullWidth from '../background-full-width/background-full-width';

interface Props {
    dark?: boolean;
    heading: string;
    children: ReactNode;
}
const IndexModule = forwardRef<HTMLDivElement,Props>((props: Props, ref) => {
    return (
        <div ref={ref} className={styles.introModule}>
            <BackgroundFullWidth dark={props.dark}/>
            <div className={styles.heading}>
            <Heading heading={props.heading}/>
        </div>
            <div className={styles.content}>
                {props.children}
            </div>
        </div>
    );
});

export default IndexModule;
