import React, {PropsWithChildren, PropsWithoutRef} from 'react';
import * as styles from './background-full-width.module.scss';

const BackgroundFullWidth = ({dark = false}: PropsWithoutRef<{dark?: boolean}>) => {
 return <div className={`${styles.background} ${dark ? styles.backgroundDark : ''}`}/>;
};

export default BackgroundFullWidth;
