import React, {PropsWithChildren, PropsWithoutRef} from 'react';
import * as styles from './heading.module.scss';
// @ts-ignore
import HopfenLeft from '../../../../svgs/hopfen/hopfen-left.svg';
// @ts-ignore
import HopfenRight from '../../../../svgs/hopfen/hopfen-right.svg';

const Heading = ({heading = '', backgroundColor}: PropsWithoutRef<{heading: string; backgroundColor?: string}>) => {
 return (
     <div className={styles.heading}>
     <HopfenLeft className={styles.hopfen}/>
     <h1>{heading}</h1>
     <HopfenRight  className={styles.hopfen}/>
     </div>
 );
};

export default Heading;
