import React from 'react';
import {graphql} from 'gatsby';
import Layout from '../components/containers/layout/Layout';
import Content, {HTMLContent} from '../components/Content';
import {Image} from '../models/Image';
import IndexModule from '../components/components/util/index-module/index-module';
import SEO from '../components/containers/seo/seo';

export const AboutPageTemplate = ({title, content, contentComponent}: AboutPageTemplateProps) => {
    const PageContent: any = contentComponent || Content;

    return (
        <>
            <SEO title={title}/>
            <div className="about-page">
                <IndexModule heading={title}>
                    <section>
                        <PageContent className="content" content={content}/>
                    </section>
                </IndexModule>
            </div>
        </>
    );
};

interface AboutPageTemplateProps {
    title: string;
    content: string;
    contentComponent?: (data: any) => any;
}

const AboutPage = ({data}: AboutPageProps) => {
    const {markdownRemark: post} = data;
    return (
        <Layout isHero={true} imageClass="about" image={post.frontmatter.image}>
            <AboutPageTemplate
                contentComponent={HTMLContent}
                title={post.frontmatter.title}
                content={post.html}
            />
        </Layout>
    );
};

interface AboutPageProps {
    data: {
        markdownRemark: {
            html: string;
            frontmatter: { title: string, image: Image };
        }
    };
}

export default AboutPage;

export const aboutPageQuery = graphql`
  query AboutPage($id: String!) {
    markdownRemark(id: { eq: $id }) {
      html
      frontmatter {
        title
        image {
          childImageSharp {
            fluid(maxWidth: 2048, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }
`;
